export default {
	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			typeList: [],
			insertList: [],
			productList: []
		};
	},

	onLoad() {
		this.loadData();
	},
	methods: {
		appRequest() {

		},
		async loadData() {
			uni.request({
				url: this.baseUrl + '/api/home/list',
				data: {},
				header: {},
				success: (res) => {
					if (res.data.success) {
						let carouselList = res.data.result.headList;
						this.titleNViewBackground = carouselList[0].bgColor;
						this.swiperLength = carouselList.length;
						this.carouselList = carouselList;
						
						this.typeList = res.data.result.categoryList;
						this.insertList = res.data.result.insertList;
						this.productList = res.data.result.productList;
					}
				}
			});


			// let carouselList = await this.$api.json('carouselList');
			// this.titleNViewBackground = carouselList[0].background;
			// this.swiperLength = carouselList.length;
			// this.carouselList = carouselList;

			// let goodsList = await this.$api.json('goodsList');
			// this.goodsList = goodsList || [];

			
		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].bgColor;
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.title;
			console.log("详情页id", id);
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		moreSellWell(){
			this.$api.msg('点击了更多热销商品');
		},
	},

























}
